export async function GET_BOOKS (request : Request) {
    try {
        const connection = await mysql.createConnection(connectionParameters)

        let get_exp_query = 'SELECT * FROM Books'

        let values: any[] = []

        const [books] = await connection.execute(get_exp_query, values)

        // connection.end() ?

        console.log(books)
        return NextResponse.json(books)
    } catch(err) {
        console.log('ERROR: API - ', (err as Error).message)
        
        const response = {
            error: (err as Error).message,

            returnedStatus: 200,
        }

        return NextResponse.json(response, { status: 200 })
    }
}